/* eslint-disable import/no-extraneous-dependencies */
import React from 'react';
import PropTypes from 'prop-types';
import { TemplateAnnotations } from '@magnolia/template-annotations';
import {
  EditorContext, constants, ComponentHelper, EditorContextHelper, PersonalizationService
} from '../../util';

export default class EditableComponent extends React.PureComponent {
  static propTypes = {
    content: PropTypes.object.isRequired
  };

  constructor(props) {
    super(props);
    this.constants = constants;
  }

  async componentDidMount() {
    await this.addComment();
    this.removeRefs();
  }

  static contextType = EditorContext;

  async addComment() {
    const { content } = this.props;
    if (!content || !this.openNode || !await EditorContextHelper.inEditorAsync()) {
      return;
    }
    const pageTemplateId = content[constants.TEMPLATE_ID_PROP];
    const { templateDefinitions, templateAnnotations } = this.context;
    if (templateDefinitions && templateDefinitions[pageTemplateId]) {
      this.openNode.parentNode.insertBefore(document.createComment(TemplateAnnotations.getComponentCommentString(content, templateDefinitions[pageTemplateId])), this.openNode);
      this.closeNode.parentNode.insertBefore(document.createComment(this.constants.CLOSED_COMPONENT_COMMENT), this.closeNode.nextSibling);
    } else if (templateAnnotations && templateAnnotations[content['@path']]) {
      this.openNode.parentNode.insertBefore(document.createComment(templateAnnotations[content['@path']]), this.openNode);
      this.closeNode.parentNode.insertBefore(document.createComment(this.constants.CLOSED_COMPONENT_COMMENT), this.closeNode.nextSibling);
    }
  }

  removeRefs() {
    if (!this.openNode || !this.closeNode) {
      return;
    }
    this.openNode.remove();
    this.closeNode.remove();
  }

  render() {
    const { content } = this.props;
    const { componentMappings, templateAnnotations } = this.context;
    const generatedAreasContent = TemplateAnnotations.generateMissingAreas(content, templateAnnotations);
    const renderingContent = templateAnnotations ? PersonalizationService.getVariant(generatedAreasContent, templateAnnotations) : generatedAreasContent;
    const component = ComponentHelper.getRenderedComponent(renderingContent, componentMappings);

    return (
      <>
        <div ref={node => this.openNode = node} />
        {component}
        <div ref={node => this.closeNode = node} />
      </>
    );
  }
}
