/* eslint-disable import/no-extraneous-dependencies */
import React from 'react';
import PropTypes from 'prop-types';
import { TemplateAnnotations } from '@magnolia/template-annotations';
import { EditableComponent } from '../EditableComponent';
import {
  EditorContext, constants, EditorContextHelper, ComponentHelper
} from '../../util';

class EditableArea extends React.PureComponent {
  static propTypes = {
    content: PropTypes.object.isRequired,
    parentTemplateId: PropTypes.string,
    className: PropTypes.any,
    elementType: PropTypes.string,
    customView: PropTypes.elementType,
    children: PropTypes.node
  };

  static defaultProps = {
    parentTemplateId: null,
    className: null,
    elementType: 'div',
    customView: null,
    children: null
  }

  constructor(props) {
    super(props);
    this.constants = constants;
  }

  async componentDidMount() {
    if (!this.context) {
      throw new Error('EditableArea component must be wrapped inside EditablePage component.');
    }
    await this.addComment();
    EditorContextHelper.refresh();
  }

  static contextType = EditorContext;

  getParentTemplateId() {
    const { parentTemplateId } = this.props;
    const { content } = this.context;

    return parentTemplateId || content[constants.TEMPLATE_ID_PROP];
  }

  async addComment() {
    const { content } = this.props;
    if (!content || !this.node || !await EditorContextHelper.inEditorAsync()) {
      return;
    }
    const pageTemplateId = this.getParentTemplateId();
    const { templateDefinitions, templateAnnotations } = this.context;
    if (templateDefinitions && templateDefinitions[pageTemplateId]) {
      this.node.parentNode.insertBefore(document.createComment(TemplateAnnotations.getAreaCommentString(content, templateDefinitions[pageTemplateId])), this.node);
      this.node.parentNode.insertBefore(document.createComment(this.constants.CLOSED_AREA_COMMENT), this.node.nextSibling);
    } else if (templateAnnotations && templateAnnotations[content['@path']]) {
      this.node.parentNode.insertBefore(document.createComment(templateAnnotations[content['@path']]), this.node);
      this.node.parentNode.insertBefore(document.createComment(this.constants.CLOSED_AREA_COMMENT), this.node.nextSibling);
    }
  }

  render() {
    const {
      content, className, elementType, children, customView: CustomView
    } = this.props;
    const componentContents = content['@nodes'].map(nodeName => content[nodeName]);
    const element = React.createElement(elementType || 'div');
    let childContent;
    if (CustomView) {
      childContent = <CustomView content={content} />;
    } else if (children) {
      childContent = React.Children.map(children, (child) => {
        if (React.isValidElement(child)) {
          return React.cloneElement(child, { content });
        }
        return child;
      });
    } else {
      childContent = componentContents.map(item => <EditableComponent key={item['@id']} content={item} />);
    }
    return (
      <element.type ref={node => this.node = node} key={content['@id']} className={ComponentHelper.classnames(className)}>
        {childContent}
      </element.type>
    );
  }
}

export default EditableArea;
